import uniLoadMore from "@/components/uni-load-more/uni-load-more.vue";
import uniIcon from "@/components/uni-icons/uni-icons.vue";
import topTabbar from "@/components/topTabbar-follow.vue";
import msDropdownMenu from '@/components/ms-dropdown/dropdown-menu.vue'
import msDropdownItem from '@/components/ms-dropdown/dropdown-item.vue'
let _this,timer;
export default {
	components:{
		uniLoadMore,
		uniIcon,
		topTabbar,
		msDropdownMenu,
		msDropdownItem
	},
	data() {
		return {
			st:2,
			green:'#7BC346',
			red:"#ff0000",
			items: [{text:'最新跟进',value: 0},{text:'待跟进',value: 1}],
			userPostList:[],
			userPostCode: 0,
			
			loadingType: 'more',
			followList:[],
			pageNum: 1,
			pageSize: 20,
			scope:0,
			keyword:'',
			isDoRefresh:false
		};
	},
	onReachBottom:function(){
		 if (timer != null) {
		            clearTimeout(timer);
		        }
		   timer = setTimeout(function() {
		        _this.fetchMoreList();
		        }, 1000);
	},
	onPullDownRefresh:function(){
		
		this.fetchList();
	},
	onLoad:function(){
		_this = this
		this.fetchList()
		this.getBuyDeputy()
	},
	onNavigationBarButtonTap:function(val){
		 
			 this.tapSettingButton() 
   
	},
	onShow:function(){
		let pages = getCurrentPages();
		let currPage = pages[pages.length-1];
		if (currPage.data.isDoRefresh == true){
			       currPage.data.isDoRefresh = false;
				   this.fetchList()
			}
	},
	methods:{
		tapSettingButton:function(){
			uni.navigateTo({
				url: './followCircle'
			})
		},
		async getBuyDeputy(){
			const res = await this.$http.get('/portrayal/deputy_base',{})
			let tmp = res.data.data.list
			tmp.forEach((item)=>{
				this.userPostList.push({text:item.label,value:item.id})
			})
			this.userPostList.unshift({text:'帮办',value: 0})
			
			console.log(this.userPostList)
			
		},
		async fetchList(){
			this.pageNum =1
			let data={
				deputyUserCode: this.userPostCode,
				scope: this.scope,
				keyword: this.keyword,
				pageNum:  this.pageNum,
				pageSize: this.pageSize,
			}
			console.log(data)
			const res = await this.$http.get('/portrayal/follow_up_list',{data:data})
			console.log(res)
			this.pageNum += 1
			
			this.followList = res.data.data.list
			uni.stopPullDownRefresh(); //得到数据后停止下拉刷新
		},
		async fetchMoreList(){
			if (_this.loadingType !== 'more') {//loadingType!=0;直接返回
				return false;
			}
			_this.loadingType = 'loading';
			uni.showNavigationBarLoading();//显示加载动画
			let data={
				deputyUserCode: this.userPostCode,
				scope: this.scope,
				keyword: this.keyword,
				pageNum:  this.pageNum,
				pageSize: this.pageSize,
			}
			const res = await this.$http.get('/portrayal/follow_up_list',{data:data})
			if (res.data.data.list.length==0){
				_this.loadingType = '';
				uni.hideNavigationBarLoading();//关闭加载动画
				return;
			}
			this.pageNum += 1
			this.followList = this.followList.concat(res.data.data.list)
			_this.loadingType = 'more';//将loadingType归0重置
			uni.hideNavigationBarLoading();//关闭加载动画
			
		},
		blurInput:function(){
			
		},
		tapSearch:function() {
		
			console.log(this.keyword)
			this.fetchList()
		
		},
		filterInput:function(){
			this.loadingType = 'more'
			this.fetchList()
		},
		toDetail:function(e){
			let pupDefault = uni.getStorageSync('pupDefault')
			console.log(pupDefault)
			if (pupDefault=='BUY_DEPUTY'){
				uni.navigateTo({
					url: `./followRecordDetail?recordCode=${e}`,
					success: res => {},
					fail: (err) => {console.log(err)},
					complete: () => {}
				});
			}else if (pupDefault=='SELL_DEPUTY'){
				uni.navigateTo({
					url: `./followRecordDetailSeller?recordCode=${e}`,
					success: res => {},
					fail: (err) => {console.log(err)},
					complete: () => {}
				});
			}
			
		},
		tabSwitch:function(index){
			this.activeIndex = index
			console.log('activeIndex',this.activeIndex)
			if (this.activeIndex ==0){
				 this.scope = 0
				}else {
					this.scope = 1
			}
			this.fetchList()
		},
		toCompanyDetail:function(companyCode){
			uni.navigateTo({
				url: `./sd_followRecordDetail?companyCode=${companyCode}`,
				success: res => {},
				fail: (err) => { console.log(err)},
				complete: () => {}
			});
		}
	}
}
